import React, { useRef } from "react";
import CubeSwiper from "./UI/CubeSwiper/CubeSwiper";

import CircleIcon from "../assets/img/circle.svg";

import SliderImg1 from "../assets/img/hero-slider/01.jpg";
import SliderImg2 from "../assets/img/hero-slider/02.jpg";
import SliderImg3 from "../assets/img/hero-slider/03.jpg";
import SliderImg4 from "../assets/img/hero-slider/04.jpg";
import SliderImg5 from "../assets/img/hero-slider/05.jpg";

import ScrollArrow from "./UI/ScrollArrow/ScrollArrow";

import { Link, animateScroll as scroll } from "react-scroll";
import VideoPlayer from "./UI/VideoPlayer/VideoPlayer";
import ButtonPrimary from "./UI/Buttons/ButtonPrimary";
import ButtonSecondary from "./UI/Buttons/ButtonSecondary";

function Home() {
	const sliderImgs = [
		SliderImg1,
		SliderImg2,
		SliderImg3,
		SliderImg4,
		SliderImg5,
	];

	return (
		<main className="main">
			<section className="hero container">
				<div className="hero__content">
					<div className="hero__links">
						<h1 className="hero__title">
							Sorgfältige Reinigung von Wohnungen <br /> und Häusern und
							verschiedene
							<br /> Dienstleistungen im NRW mit 20 Jahren
							<br /> Erfahrung
							<span>mit vertraglichen Qualitätsgarantien</span>
						</h1>
						<h2 className="hero__subtitle">
							Sie können wählen, ob Sie uns alle Arbeiten übertragen, <br />
							oder einfach nur eine helfende Hand brauchen, die mit <br />
							anpackt.
						</h2>
						<div className="hero__group-btn">
							<ButtonPrimary>Berechnen Sie die Kosten</ButtonPrimary>
							<ButtonSecondary>Reinigungsart auswählen</ButtonSecondary>
						</div>
					</div>
					<div className="hero__right">
						<CubeSwiper className="hero-swiper" images={sliderImgs} />
						<div className="hero__number">
							<img
								src={CircleIcon}
								alt="Circle"
								className="hero__number-icon"
							/>
							<div className="hero__number-text">
								Mehr <span>als 535</span>
								<br /> Objekte geräumt
							</div>
						</div>
					</div>
				</div>
			</section>
			<div className="scroll-elem">
				<Link to="video" spy={true} smooth={true} duration={500}>
					<ScrollArrow />
				</Link>
			</div>

			<section id="video" className="video container">
				<div className="video__content">
					<VideoPlayer />
				</div>
			</section>
			<section className="service container">
				<div className="service__content"></div>
			</section>
		</main>
	);
}

export default Home;
